(function() {
  'use strict';

  angular
    .module('englishGalore')
    .directive('exerciseShow', exerciseShow);

  /** @ngInject */
  function exerciseShow() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/exercise/exerciseShow/exerciseShow.html',
      controller: ExerciseController,
      controllerAs: 'exerciseShow',
      bindToController: true,
      scope:{
        model: '=',
        section: '@',
        currentsection: '='
      }
    };

    return directive;

    /** @ngInject */
    function ExerciseController(models, $state, units, $q, $scope, $uibModal) {
      var vm = this;
      vm.models = models;

      vm.data = {
         availableOptions: [
             {id: 1 , name: 'Dropdown'},
             {id: 2 , name: 'Completamento'},
             {id: 3 , name: 'Trova lintruso'},
             {id: 4 , name: 'Riordina la parola'},
             {id: 5 , name: 'Riordina le frasi'},
             {id: 6 , name: 'Connetti immagine/frase'},
             {id: 7 , name: 'Connetti frase/frase'},
             {id: 8 , name: 'Vero o Falso'},
             {id: 9 , name: 'Memory'},
             {id: 10 , name: 'Dialogo'}
          ]
      };

      vm.setLoading = function (res) {
        vm.loading = true;
        return $q.resolve(res);
      }

      vm.unsetLoading = function (res) {
        vm.loading = false;
        return $q.resolve(res);
      }

      vm.uploadImageTipFile = function($file){
        vm.form.image_tip = $file;
        vm.form.exercise_tips_image_filename = $file.name;
      }

      vm.submitImageTip = function(id){
        var exercise_tips_image_url = "";
        var exercise_tips_image_filename = "";
        return units
          .uploadImageTipExercise(id, { filename: vm.form.image_tip.name, type: vm.form.image_tip.type })
          .then(function(res) {
            exercise_tips_image_filename = res.exercise_tips_image_filename;
            exercise_tips_image_url = res.exercise_tips_image_url;
            return units.uploadFileS3(res.upload_url, vm.form.image_tip.type, vm.form.image_tip)
          })
          .then(function(){
            vm.form.exercise_tips_image_url = exercise_tips_image_url;
            vm.form.exercise_tips_image_filename = exercise_tips_image_filename;
            vm.model.exercise_tips_image_url = exercise_tips_image_url;
            vm.model.exercise_tips_image_filename = exercise_tips_image_filename;
            delete vm.form.image_tip;
          });
      }


      vm.submit = function ($event) {

        vm.form.exercise_type = vm.exercise_type_temp.id;

        if(vm.exercise_text_text_items){
          if(vm.form.exercise_image_text_items){
            delete vm.form.exercise_image_text_items;
          }
          if(vm.form.exercise_text){
            delete vm.form.exercise_text;
          }
          vm.form.exercise_text_text_items = angular.copy(vm.exercise_text_text_items);
        }
        else if(vm.exercise_image_text_items){
          if(vm.form.exercise_text_text_items){
            delete vm.form.exercise_text_text_items;
          }
          if(vm.form.exercise_text){
            delete vm.form.exercise_text;
          }
          vm.form.exercise_image_text_items = angular.copy(vm.exercise_image_text_items);
        }
        else if(vm.exercise_text){
          if(vm.form.exercise_image_text_items){
            delete vm.exercise_image_text_items;
          }
          if(vm.form.exercise_text_text_items){
            delete vm.form.exercise_text_text_items;
          }
          vm.form.exercise_text = angular.copy(vm.exercise_text);
        }

            var uploadPromises = [];
            if(vm.form.exercise_image_text_items){
              vm.setLoading();
                //se non ho ancora l'item.id devo fare prima una create
                angular.forEach(vm.form.exercise_image_text_items, function(item){
                    if(!item.id){
                      var item_temp = {
                          'item_number': item.item_number,
                          'image_url': '',
                          'image_filename': item.image.file.name,
                          'text_en': item.text_en
                      };

                    var promise = units
                        .createExerciseImage(vm.form.id, {'exercise_image_text_item': item_temp})
                        .then(function(res){
                          item.id = res.exercise_image_text_item.id;
                        })
                    uploadPromises.push(promise);
                  }
                });
            }
            //return vm.setLoading()
          return $q
          .all(uploadPromises)
          .then(function(){
            if(vm.form.image_tip && vm.form.image_tip.name){
              return vm.submitImageTip(vm.form.id);
            }
          })
          .then(function(){
            return units.updateExercise(vm.form.id,  {'exercise':vm.form})
            })
          .then(function(res){
            angular.forEach(vm.currentsection, function(value, key) {
              if(res.exercise.id == value.id){
                vm.currentsection[key] = angular.copy(res.exercise);
                return;
              }
            });
          })
          .finally(function(){
            vm.unsetLoading
            $event.preventDefault();
            $event.stopPropagation();
          });
      }

      vm.destroy = function() {
        $uibModal.open({
            templateUrl: 'app/components/exercise/destroy/destroy.html',
            controller: 'ExerciseDestroyController',
            controllerAs: 'exerciseDestroy',
            windowClass: 'modal-default'
          }).result
          .then(function (res) {
            if(res === true){
              return units
                  .destroyExercise(vm.form)
                  .then(function(){
                    angular.forEach(vm.currentsection, function(value, key) {
                      if(vm.form.id == value.id){
                        vm.currentsection.splice(key,1);
                        return;
                      }
                    });
                  })
            }
          })
      }

      $scope.$watch('exerciseShow.model', function (newVal, oldVal) {
        if(newVal !== oldVal) {
            vm.form = angular.copy(vm.model);
        }
      });

    }

  }

})();

