'use strict';

angular.module('englishGalore')
  /**
   * UI state service.
   * Keep application-wide shared variables
   */
  .factory('utils', function () {
    var utils = {
      /**
       * Find the first occurrence of an object (given an 'id' field) in an array
       */
      findFirst: function(array,id) {
        for (var i = 0; i < array.length; i++) {
          if(array[i].id === id){
            return array[i];
          }
        }
        return null;
      },

      /**
       * Find the first occurrence of an object (given a 'name' field) in an array
       */
      findFirstByName: function(array,name) {
        for (var i = 0; i < array.length; i++) {
          if(array[i].name === name){
            return array[i];
          }
        }
        return null;
      },

      /**
      * Remove the first occurence of a an object (given an 'id' field) in an array
      */
      removeFirst: function (array, id) {
        var found = false;
        for (var i = 0; i < array.length; i++) {
          if(array[i].id === id){
            found = i;
            break;
          }
        }
        array.splice(found,1);
      },

      /**
       * Sort given array by 'order' key
       */
      sortByOrder: function(array) {
        if(angular.isArray(array)){
          return array.sort(function(a,b) {
            if(a.order > b.order){
              return 1;
            }
            if(a.order < b.order){
              return -1;
            }
            return 0;
          });
        }
        return array;
      },

      /**
       * Try to merge elem to a target object with the same id in array
       * If the target doesn't exist, push elem in the array
       */
      mergeOrPush: function(array,elem) {
        var added = utils.merge(array, elem);
        if(!added){
          added = elem;
          array.push(added);
        }
        return added;
      },

      /**
       * Merge elem to a target object with the same id in array
       * If the target doesn't exist, return null
       */
      merge: function(array, elem) {
        var found = utils.findFirst(array, elem.id);
        if(found){
          angular.merge(found, elem);
        }
        return found;
      }
    };

    return utils;
  });

(function() {
  'use strict';

  angular
    .module('englishGalore')
    .directive('file', file);

  /** @ngInject */
  function file() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/uploadFile/uploadFile.html',
      link: UploadFileLink,
      scope:{
        model: '='
      }
    };

    return directive;


    function UploadFileLink(scope, el) {
      el.bind('change', function(event){
          var files = event.target.files;
          var file = files[0];
          scope.model.file = file;
          scope.$apply();
        });
    }

  }

})();

