(function() {
  'use strict';

  angular
    .module('englishGalore')
    .directive('toggleSwitch', toggleSwitch);

  /** @ngInject */
  function toggleSwitch() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/toggleSwitch/toggleSwitch.html',
      controller: ToggleSwitchController,
      controllerAs: 'toggleSwitch',
      bindToController: true,
      scope:{
        model: '='
      }
    };

    return directive;

    /** @ngInject */
    function ToggleSwitchController(models, units){
      var vm = this;
      vm.models = models;

      vm.publish = function(){
        units
          .publishUnit(vm.model.id)
          .then(function(){
            vm.model.published = true;
          });
      }

      vm.unpublish = function(){
        units
        .unpublishUnit(vm.model.id)
        .then(function(){
          vm.model.published = false;
        });
      }
    }
  }

})();

