(function() {
  'use strict';

  angular
    .module('englishGalore')
    .directive('exercise', exercise);

  /** @ngInject */
  function exercise() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/exercise/exercise.html',
      controller: TabExerciseController,
      controllerAs: 'exercise',
      bindToController: true,
      scope:{
        model: '=',
        section: '@',
        currentsection: '='
      }
    };

    return directive;

    /** @ngInject */
    function TabExerciseController(models, $state, units, $q) {
      var vm = this;
      vm.models = models;

      vm.data = {
         availableOptions: [
             {id: 1 , name: 'Dropdown'},
             {id: 2 , name: 'Completamento'},
             {id: 3 , name: 'Trova lintruso'},
             {id: 4 , name: 'Riordina la parola'},
             {id: 5 , name: 'Riordina le frasi'},
             {id: 6 , name: 'Connetti immagine/frase'},
             {id: 7 , name: 'Connetti frase/frase'},
             {id: 8 , name: 'Vero o Falso'},
             {id: 9 , name: 'Memory'},
             {id: 10 , name: 'Dialogo'}
          ],
         selectedOption: {id: 1, name: 'Dropdown'}
      };

      vm.setLoading = function (res) {
        vm.loading = true;
        return $q.resolve(res);
      }

      vm.unsetLoading = function (res) {
        vm.loading = false;
        return $q.resolve(res);
      }

      vm.uploadImageTipFile = function($file){
        vm.form.image_tip = $file;
        vm.form.exercise_tips_image_filename = $file.name;
      }

      vm.submitImageTip = function(id){
        var exercise_tips_image_url = "";
        var exercise_tips_image_filename = "";
        return units
          .uploadImageTipExercise(id, { filename: vm.form.image_tip.name, type: vm.form.image_tip.type })
          .then(function(res) {
            exercise_tips_image_filename = res.exercise_tips_image_filename;
            exercise_tips_image_url = res.exercise_tips_image_url;
            return units.uploadFileS3(res.upload_url, vm.form.image_tip.type, vm.form.image_tip)
          })
          .then(function(){
            vm.form.exercise_tips_image_url = exercise_tips_image_url;
            vm.form.exercise_tips_image_filename = exercise_tips_image_filename;
            vm.model.exercise_tips_image_url = exercise_tips_image_url;
            vm.model.exercise_tips_image_filename = exercise_tips_image_filename;
            delete vm.form.image_tip;

            return units.updateExercise(vm.form.id,  {'exercise':vm.form});
          });
      }

      vm.submit = function () {

        vm.form.unit_section_id = vm.model.id;
        vm.form.unit_section_type = vm.section;
        vm.form.exercise_type = vm.form.exercise_type.id;

        delete vm.form.exercise_text_text;
        delete vm.form.exercise_image_text;
        delete vm.form.exercise_text;

        return vm.setLoading()
          .then(function(){
            return units.createExercise({'exercise': vm.form});
           })
          .then(function(res){
            vm.form.id = res.exercise.id;

            if(vm.form.image_tip && vm.form.image_tip.name){
              return vm.submitImageTip(vm.form.id);
            }
          })
          .then(function(){
            vm.currentsection.push(angular.copy(vm.form));
            vm.models.activeTabIndex = 0;
            vm.form.exercise_tips = "";
            vm.form.title = "";
            vm.form.subtitle = "";
            vm.form.exercise_type = {id: 1, name: 'Dropdown'};
          })
          .finally(vm.unsetLoading);
      }
    }

  }

})();

'use strict';

angular.module('englishGalore')
  /**
   * Error Manager.
   * Handles common HTTP error messages
   */
  .factory('errorManager', function (toastr, $log, $state, $q, longTermMemory, models) {
    var errorManager = function(err) {
      // log every errors
      $log.error(err);

      // Unwrap HTTP errors and take actions if needed
      if(angular.isObject(err) && err.status){
        switch(err.status){
          case 401:
            $state.go('main.logout');
            err = err.data;
            break;
          case 403:
            $state.go('main.home');
            err = err.data;
            break;
          case 422:
            err = err.data;
            break;
          case 404:
            err = err.data;
            break;
          case 413:
            err = 'Il file che stai cercando di caricare  troppo grande';
            break;
          case -1:
            err = 'Il server non  raggiungibile o non sei connesso ad Internet';
            break;
          default:
            break;
        }
      }

      var getErrorString = function (err) {
        if(angular.isString(err)){
          return err;
        }
        else if(angular.isObject(err) && (err.full_messages || err.error_description || err.errors || err.data || err.msg || err.error)){
          return getErrorString(err.full_messages || err.error_description || err.errors || err.data || err.msg || err.error );
        }
        else if(angular.isArray(err)){
          var myErr = [];
          for (var i = 0; i < err.length; i++) {
            myErr[i] = getErrorString(err[i]);
          }
          return getErrorString(myErr.join('<br>'));
        }
        // Don't manage different kinds of error
        else{
          longTermMemory.oblivion(); // Forget everything. We don't want the app to be broken forever!
          models.reset(); // Reset all the models. We don't want the app to be broken forever!
          return 'Si  verificato un errore imprevisto. Si prega di riprovare pi tardi.';
        }
      }

      toastr.error(getErrorString(err), 'Errore');

      return $q.reject(err);
    };

    return errorManager;
  });

